
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-left: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  max-width: 98vw;
}

.plot-div {
  position: relative;
}
.plot-close {
  position: absolute;
  z-index: 1;
}

.tab {
  cursor: pointer;
}
.tab-table {
  max-height: 87vh;
}
.tab-table-item {
  padding-left: 0px;
}
.var-dropdown {
  cursor: pointer;
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}

.plots-div {
  max-height: 85vh;
  overflow-y: auto;
  border: 1px blue;
  border-style: solid;
  padding-right: 15px;
  border-radius: 10px;
}
